.bellAnimation {
  -webkit-animation-name: bell-resizing;
          animation-name: bell-resizing;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  }

@-webkit-keyframes bell-resizing {
  0% {height: 9vw; width: 9vw; left: 44.3%}
  90% {height: 9vw; width: 9vw; left: 44.3%}
  93% {height: 13vw; width: 13vw; left: 43%}
